import Home from '../Pages/Home/Home';
import Nouns from '../Pages/Nouns/Nouns';
import Verbs from "../Pages/Verbs/Verbs";

const homePath = '/';
const nounsPath = '/nouns';
const verbsPath = '/verbs';

export const routes = {
    home: {
        path: homePath,
        component: Home
    },
    nouns: {
        path: nounsPath,
        component: Nouns
    },
    verbs: {
        path: verbsPath,
        component: Verbs
    }
};