import React, {useState, useEffect} from 'react';
import './NavigationBar.css'
import {Link} from "react-router-dom";
import {routes} from "../../../Routes/Routes";

function NavigationBar(props) {

    const [full, setFull] = useState(true);
    const handleScroll = () => {
        setFull(window.pageYOffset === 0);
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);

        return () => window.removeEventListener('scroll', handleScroll);
    });

    return (
        <nav className={'navigationBar'} style={{
            // transform: `scale(${full?1:0})`
        }}>
            <ul>
                <li><Link to={routes.home.path}>Home</Link></li>
                <li><Link to={routes.nouns.path}>Nouns</Link></li>
                <li><Link to={routes.verbs.path}>Verbs</Link></li>
            </ul>
        </nav>
    )
}


export default NavigationBar;